@import "animate.css";
@import "common";
@import "./fonts/iconfont.css";
@import "./fonts1/iconfont.css";
@import "antd-mobile.css";
@import "./swiper.min.css";


$base_ft:20px;
/*no*/
$base_color:red;

html,
body,
#app,
.main {
    width: 100vw;
    height: 100vh;
    background-color: #fff;
}

* {
    touch-action: pan-y;
}


